.scroll {
  margin-top: 47px;
}

.scroll__content {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  column-gap: 30px;
  row-gap: 30px;
}

.card {
  position: relative;
  width: 100%;
  height: 225px;
  transition: transform 0.3s ease;
}

.card__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card:hover {
  transform: scale(1.1);
}

.heart__icon {
  position: absolute;
  cursor: pointer;
  right: 19px;
  bottom: 23px;
  width: 38px;
  height: auto;
  stroke: #f24e1e;
  fill: transparent;
  stroke-width: 4px;
  opacity: 0;
  transition: opacity 0.3s ease, fill 0.2s ease-in, transform 0.3s ease;
}

.heart__icon:hover {
  fill: #f24e1e;
}

.heart__icon:active {
  transform: scale(1.2);
}

.heart__icon.active {
  fill: #f24e1e;
}

.card:hover .heart__icon {
  opacity: 1;
}
